"use client";

import { useEffect, useMemo, useRef, useState } from "react";
import NotesLayout from "@/components/notes/Layout";
import GameShell from "@/components/play/GameShell";
import GameFooter from "@/components/play/GameFooter";

type Card = { id: number; label: string; matched: boolean };

type DifficultyId = "easy" | "standard" | "hard";

const DIFFICULTIES: Record<DifficultyId, { label: string; pairs: string[]; gridCols: number }> = {
  easy: { label: "Easy", pairs: ["A", "B", "C", "D", "E", "F"], gridCols: 3 },
  standard: { label: "Standard", pairs: ["A", "B", "C", "D", "E", "F", "G", "H"], gridCols: 4 },
  hard: { label: "Hard", pairs: ["A", "B", "C", "D", "E", "F", "G", "H", "I", "J"], gridCols: 5 },
};

const LS_BEST_KEY = "rn_play_memory_match_best_v1";
const LS_LAST_PLAYED_KEY = "rn_play_memory_match_last_played_v1";

function shuffledDeck(pairs: string[]): Card[] {
  const deck = [...pairs, ...pairs].map((label, idx) => ({ id: idx + 1, label, matched: false }));
  for (let i = deck.length - 1; i > 0; i -= 1) {
    const j = Math.floor(Math.random() * (i + 1));
    [deck[i], deck[j]] = [deck[j], deck[i]];
  }
  return deck;
}

function safeReadJson<T>(key: string): T | null {
  try {
    const raw = window.localStorage.getItem(key);
    if (!raw) return null;
    return JSON.parse(raw) as T;
  } catch {
    return null;
  }
}

function safeWriteJson(key: string, value: unknown) {
  try {
    window.localStorage.setItem(key, JSON.stringify(value));
  } catch {
    // ignore
  }
}

export default function MemoryMatchPage() {
  const [difficulty, setDifficulty] = useState<DifficultyId>("standard");
  const [cards, setCards] = useState<Card[]>(() => shuffledDeck(DIFFICULTIES.standard.pairs));
  const [flipped, setFlipped] = useState<number[]>([]);
  const [locked, setLocked] = useState(false);
  const [attempts, setAttempts] = useState(0);
  const [matches, setMatches] = useState(0);
  const startedAtRef = useRef<number | null>(null);
  const [best, setBest] = useState<any>(null);

  useEffect(() => {
    const b = safeReadJson(LS_BEST_KEY);
    if (b) setBest(b);
  }, []);

  useEffect(() => {
    safeWriteJson(LS_LAST_PLAYED_KEY, { at: new Date().toISOString() });
    // Server persistence could be added later, but this phase stays local only by design.
  }, []);

  useEffect(() => {
    if (flipped.length === 2) {
      const [firstId, secondId] = flipped;
      const first = cards.find((c) => c.id === firstId);
      const second = cards.find((c) => c.id === secondId);
      if (!first || !second) return;

      setAttempts((a) => a + 1);

      if (first.label === second.label) {
        setCards((prev) => prev.map((c) => (c.id === firstId || c.id === secondId ? { ...c, matched: true } : c)));
        setMatches((m) => m + 1);
        setFlipped([]);
      } else {
        setLocked(true);
        setTimeout(() => {
          setFlipped([]);
          setLocked(false);
        }, 650);
      }
    }
  }, [flipped, cards]);

  const completed = useMemo(() => cards.length > 0 && cards.every((c) => c.matched), [cards]);

  const accuracy = useMemo(() => {
    if (attempts <= 0) return null;
    return Math.max(0, Math.min(1, matches / attempts));
  }, [attempts, matches]);

  const elapsedMs = useMemo(() => {
    if (!completed) return null;
    if (startedAtRef.current == null) return null;
    return Date.now() - startedAtRef.current;
  }, [completed]);

  useEffect(() => {
    if (!completed) return;
    if (elapsedMs == null) return;

    const score = Math.max(0, Math.round(1000 - attempts * 30 - elapsedMs / 40));
    const entry = { score, timeMs: elapsedMs, accuracy, at: new Date().toLocaleString() };
    const current = safeReadJson<any>(LS_BEST_KEY);

    const better =
      !current ||
      (typeof current.score === "number" && score > current.score) ||
      (score === current?.score && typeof current.timeMs === "number" && elapsedMs < current.timeMs);

    if (better) {
      safeWriteJson(LS_BEST_KEY, entry);
      setBest(entry);
    }
  }, [completed, elapsedMs, attempts, accuracy]);

  const handleFlip = (id: number) => {
    if (locked) return;
    if (flipped.includes(id)) return;
    if (startedAtRef.current == null) startedAtRef.current = Date.now();
    setFlipped((prev) => (prev.length === 2 ? [id] : [...prev, id]));
  };

  const reset = () => {
    startedAtRef.current = null;
    setCards(shuffledDeck(DIFFICULTIES[difficulty].pairs));
    setFlipped([]);
    setLocked(false);
    setAttempts(0);
    setMatches(0);
  };

  const start = () => {
    reset();
  };

  useEffect(() => {
    // When difficulty changes, reset the deck and counters.
    reset();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [difficulty]);

  const scoreNow = useMemo(() => {
    if (!completed || elapsedMs == null) return null;
    return Math.max(0, Math.round(1000 - attempts * 30 - elapsedMs / 40));
  }, [completed, elapsedMs, attempts]);

  const gridCols = DIFFICULTIES[difficulty].gridCols;

  return (
    <NotesLayout
      meta={{
        title: "Memory match",
        description: "A light matching game for focus and pattern recall.",
        level: "Summary",
        slug: "/play/memory-match",
        section: "ai",
      }}
      activeLevelId="summary"
      useAppShell
    >
      <GameShell
        title="Memory match"
        description="Flip cards to find pairs. It is calm, short, and designed for a quick mental reset."
        howToPlay={{
          objective: "Find all matching pairs with as few attempts as you can.",
          how: ["Choose a difficulty.", "Flip two cards.", "If they match, the pair stays revealed.", "Finish when all pairs are found."],
          improves: ["Short-term memory", "Attention and recall", "Pattern recognition under light pressure"],
        }}
        difficulty={{
          options: (Object.keys(DIFFICULTIES) as DifficultyId[]).map((id) => ({ id, label: DIFFICULTIES[id].label })),
          value: difficulty,
          onChange: (v) => setDifficulty(v as DifficultyId),
        }}
        onReset={reset}
        onStart={start}
        startLabel="New shuffle"
        resetLabel="Reset"
        score={{
          scoreLabel: "Score",
          score: scoreNow,
          timeMs: elapsedMs,
          accuracy,
          personalBest: best,
        }}
      >
        <div className="space-y-3">
          <div className="text-sm text-slate-800">
            {completed ? "All pairs found. Nice work." : "Find all matching pairs. Large tap targets for mobile."}
          </div>

          <div className="flex justify-center">
            <div
              className="grid gap-3"
              style={{
                gridTemplateColumns: `repeat(${gridCols}, minmax(0, 1fr))`,
                width: "100%",
                maxWidth: 520,
              }}
              aria-label="Memory matching game"
            >
              {cards.map((card) => {
                const isFlipped = flipped.includes(card.id) || card.matched;
                return (
                  <button
                    key={card.id}
                    className={`memory-card ${isFlipped ? "memory-card--flipped" : ""} ${card.matched ? "memory-card--matched" : ""}`}
                    onClick={() => handleFlip(card.id)}
                    disabled={card.matched || locked}
                    aria-label={card.matched ? `Matched card ${card.label}` : "Card face down"}
                  >
                    <span className="memory-card__face">{isFlipped ? card.label : "?"}</span>
                  </button>
                );
              })}
            </div>
          </div>
        </div>
      </GameShell>

      <GameFooter onReplay={start} />

      <style jsx>{`
        .memory-card {
          height: 86px;
          border-radius: 14px;
          border: 1px solid #e2e8f0;
          background: linear-gradient(135deg, #f8fafc, #e2e8f0);
          box-shadow: 0 10px 30px rgba(15, 23, 42, 0.06);
          display: inline-flex;
          align-items: center;
          justify-content: center;
          font-size: 22px;
          font-weight: 700;
          color: #0f172a;
          transition: transform 0.12s ease, box-shadow 0.12s ease, background 0.12s ease;
        }
        .memory-card:hover:not(:disabled) {
          background: #e0f2fe;
          transform: translateY(-1px);
        }
        .memory-card:focus-visible {
          outline: 2px solid #0ea5e9;
          outline-offset: -2px;
        }
        .memory-card--flipped {
          background: #fff;
          box-shadow: 0 8px 20px rgba(15, 23, 42, 0.08);
        }
        .memory-card--matched {
          background: #dcfce7;
          color: #0f172a;
          box-shadow: inset 0 0 0 2px #22c55e;
        }
        .memory-card__face {
          display: inline-block;
          min-width: 12px;
        }
        @media (max-width: 640px) {
          .memory-card {
            height: 72px;
            font-size: 20px;
          }
        }
      `}</style>
    </NotesLayout>
  );
}


